<template>
  <van-cell-group>
    <van-field v-model="name" label="" placeholder="" required/>
    <van-radio-group v-model="gender">
      <van-cell-group>
        <van-cell class="required">
          <div class="van-cell__title label">
            <span class="van-cell__text"></span>
          </div>
          <van-radio name="" class="radio"></van-radio>
          <van-radio name="" class="radio"></van-radio>
        </van-cell>
      </van-cell-group>
    </van-radio-group>
    <van-field v-model="stuId" label="" placeholder="" required/>
    <van-cell title="" @click="pickMajor" style="padding: 10px 40px" :value="major">
      <van-icon slot="right-icon" name="arrow" class="van-cell__right-icon" />
    </van-cell>
    <van-field v-model="className" label="" placeholder="" />
    <van-field v-model="phoneNumber" placeholder="" label="" />
    <van-field v-model="wx" placeholder="" label="" />
    <van-cell title="" @click="pickBirthday" style="padding: 10px 40px" :value="birthday">
      <van-icon slot="right-icon" name="arrow" class="van-cell__right-icon" />
    </van-cell>
    <van-field v-model="volk" label="" placeholder="" />
    <van-field v-model="nativePlace" label="" placeholder="" />
    <van-cell title="" @click="pickParty" style="padding: 10px 40px" :value="partyMember">
      <van-icon slot="right-icon" name="arrow" class="van-cell__right-icon" />
    </van-cell>
    <van-popup v-model="birthdayPicker" position="bottom" :overlay="true">
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="getBirthday"
        @cancel="cancelPickBirthday"
      />
    </van-popup>
    <van-popup v-model="majorPicker" position="bottom" :overlay="true">
      <van-picker show-toolbar :columns="majors" @confirm="getMajor" @cancel="cancelPickMajor" />
    </van-popup>
    <van-popup v-model="partyPicker" position="bottom" :overlay="true">
      <van-picker show-toolbar :columns="partys" @confirm="getParty" @cancel="cancelPickParty" />
    </van-popup>
    <!-- <van-field v-model="birthday" label="" placeholder="1997-06-13" /> -->
    <!-- <van-radio-group v-model="minority">
      <van-cell-group>
        <van-cell>
          <div class="van-cell__title label">
            <span class="van-cell__text"></span>
          </div>
          <van-radio name="" class="radio"></van-radio>
          <van-radio name="" class="radio"></van-radio>
        </van-cell>
      </van-cell-group>
    </van-radio-group>
    <van-field v-model="dormId" label="" placeholder="" />
    <van-field v-model="enrolment" label="" placeholder="" />
    <van-radio-group v-model="NDstudent">
      <van-cell-group>
        <van-cell>
          <div class="van-cell__title label">
            <span class="van-cell__text"></span>
          </div>
          <van-radio name="" class="radio"></van-radio>
          <van-radio name="" class="radio"></van-radio>
        </van-cell>
      </van-cell-group>
    </van-radio-group>
    <van-radio-group v-model="partyMember">
      <van-cell-group>
        <van-cell>
          <div class="van-cell__title label">
            <span class="van-cell__text"></span>
          </div>
          <van-radio name="" class="radio"></van-radio>
          <van-radio name="" class="radio"></van-radio>
        </van-cell>
      </van-cell-group>
    </van-radio-group> -->
  </van-cell-group>
</template>

<script>
export default {
  data() {
    return {
      name: '',
      gender: '',
      stuId: '',
      major: '',
      className: '',
      phoneNumber: '',
      wx: '',
      birthday: '',
      // minority: '',
      volk: '',
      nativePlace: '',
      // dormId: '',
      // enrolment: '',
      // NDstudent: '',
      partyMember: '',
      birthdayPicker: false,
      majorPicker: false,
      currentDate: '',
      minDate: new Date(1989,12,1),
      maxDate: new Date(2000,11,31),
      majors: ['','','','','','','','','','','','',''],
      partyPicker: false,
      partys: [ '', '', '', '' ],
    }
  },
  methods: {
    pickBirthday() {
      this.birthdayPicker = true;
    },
    getBirthday(val) {
      const birthday = `${val.getFullYear()}-${val.getMonth() < 10?'0'+(val.getMonth()+1):(val.getMonth()+1)}-${val.getDate() < 10?'0'+val.getDate():val.getDate()}`;
      this.birthday = birthday;
      this.birthdayPicker = false;
    },
    cancelPickBirthday() {
      this.birthdayPicker = false;
    },
    pickMajor() {
      this.majorPicker = true;
    },
    getMajor(val) {
      this.major = val;
      this.majorPicker = false;
    },
    cancelPickMajor() {
      this.majorPicker = false;
    },
    pickParty() {
      this.partyPicker = true;
    },
    getParty(val) {
      this.partyMember = val;
      this.partyPicker = false;
    },
    cancelPickParty() {
      this.partyPicker = false;
    },
  },
  mounted() {
      const form = JSON.parse(localStorage.getItem('basic'));
      if (form) {
        this.name = form.name;
        this.gender = form.gender;
        this.stuId = form.stuId;
        this.major = form.major;
        this.className = form.className;
        this.phoneNumber = form.phoneNumber;
        this.wx = form.wx;
        this.birthday = form.birthday;
        // this.minority = form.minority;
        this.volk = form.volk;
        this.nativePlace = form.nativePlace;
        // this.dormId = form.dormId;
        // this.enrolment = form.enrolment;
        // this.NDstudent = form.NDstudent;
        this.partyMember = form.partyMember;
      }
  }
}
</script>

<style scoped>
.van-cell__value--alone {
  display: flex;
}
.label {
  align-self: center;
}
.van-cell__title {
  min-width: 90px;
  display: inline-block;
  text-align: center;
  line-height: 32px;
}
.radio {
  display: inline-block;
  padding: 0 5px;
}
.required::before {
  content: '*';
  position: absolute;
  left: 7px;
  padding-left: inherit;
  font-size: 14px;
  color: #f44;
}
.van-cell--required::before {
  padding-left: inherit;
}
</style>



