<template>
  <van-collapse v-model="activeName" accordion>
    <van-collapse-item title="" name="1">
      <van-cell-group>
        <van-cell title="" :value="basicInfo.major" />
        <van-cell title="" :value="basicInfo.className" />
        <van-cell title="" :value="basicInfo.phoneNumber" />
        <van-cell title="" :value="basicInfo.wx" />
        <van-cell title="" :value="basicInfo.birthday" />
        <van-cell title="" :value="basicInfo.volk" />
        <van-cell title="" :value="basicInfo.nativePlace" />
        <van-cell title="" :value="basicInfo.partyMember" />
        <!-- <van-cell title="" :value="basicInfo.minority" />
        <van-cell title="" :value="basicInfo.dormId" />
        <van-cell title="" :value="basicInfo.enrolment" />
        <van-cell title="" :value="basicInfo.NDstudent" /> -->
      </van-cell-group>
    </van-collapse-item>
    <!-- <van-collapse-item title="" name="2">
      <van-cell-group>
        <van-cell title="QQ" :value="personalInfo.QQ" />
        <van-cell title="" :value="personalInfo.EMail" />
        <van-cell title="" :value="personalInfo.wx" />
        <van-cell title="" :value="personalInfo.familyAddress" />
      </van-cell-group>
    </van-collapse-item> -->
  </van-collapse>
</template>

<script>
export default {
  props: [ 'basicInfo' ],
  data() {
    return {
      activeName: '0'
    }
  }
}
</script>

<style>

</style>



