<template>
  <van-cell-group>
    <van-field v-model="phoneNumber" placeholder="" label="" />
    <van-field v-model="QQ" placeholder="QQ" label="QQ" />
    <van-field v-model="EMail" placeholder="" label=""/>
    <van-field v-model="wx" placeholder="" label="" />
    <van-field v-model="familyAddress" placeholder="" label=""  type="textarea"/>
  </van-cell-group>
</template>

<script>
export default {
  data() {
    return {
      phoneNumber: '',
      QQ: '',
      EMail: '',
      wx: '',
      familyAddress: '',
    }
  },
  mounted() {
    const form = JSON.parse(localStorage.getItem('personal'));
    if (form) {
      this.phoneNumber = form.phoneNumber;
      this.QQ = form.QQ;
      this.EMail = form.EMail;
      this.wx = form.wx;
      this.familyAddress = form.familyAddress;
    }
  }
}
</script>

<style>

</style>



