<template>
  <el-table
  :data="requiredData"
  style="width: 100%">
    <el-table-column type="expand">
      <template slot-scope="props">
        <el-form label-position="left" inline class="demo-table-expand" v-for="item in props.row.certificates" :key="item._id" style="text-align: left">
          <el-form-item label=":">
            <span>{{ item.year + item.term }}</span>
          </el-form-item>
          <el-form-item label=":">
            <span>{{ item.name }}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column
      label=""
      prop="name">
    </el-table-column>
    <el-table-column
      label=""
      prop="stuId">
    </el-table-column>
    <el-table-column label="" v-if="needOperator">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleSuccess(scope.$index, scope.row)"></el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleFail(scope.$index, scope.row)"></el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import axios from 'axios';
export default {
  props: ['requiredData', 'needOperator'],
  methods: {
    async handleSuccess(index, row) {
      const res = await axios.post('/admin/certificate/pass', { id: row._id });
      if (res.data.message === 'ok') {
        this.$message({
          type: 'success',
          message: ''
        });
        this.$emit('refresh');
      }
    },
    async handleFail(index, row) {
      const res = await axios.post('/admin/certificate/fail', { id: row._id });
      if (res.data.message === 'ok') {
        this.$message({
          type: 'success',
          message: ''
        });
        this.$emit('refresh');
      }
    }
  }
}
</script>

<style>

</style>



